@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);

body {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100vh;
  font-family: "Lobster", cursive;
  color: #2d3436;
}
.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.box {
  height: 20rem;
  border-radius: 0.5rem;
  box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.79);
  text-align: center;
  margin: auto;
  border-top-left-radius: 0.3rem;
  border-top-right-radius: 0.3rem;
}
.person {
  margin-right: 0.7rem;
  /* transform: translateX(1rem); */
  -webkit-animation: animo_1 5s linear 2s infinite alternate;
          animation: animo_1 5s linear 2s infinite alternate;
}

input[type="text"] {
  padding: 10px;
  font-size: 17px;
  border: 1px solid grey;
  float: left;
  width: 80%;
  background: #f1f1f1;
}
button {
  float: left;
  width: 20%;
  padding: 10px;
  background: #2196f3;
  color: white;
  font-size: 17px;
  border: 1px solid grey;
  border-left: none;
  cursor: pointer;
}
button:hover {
  background: #0b7dda;
}
.search {
  display: -webkit-flex;
  display: flex;
  padding: 0.5rem;
  padding-top: 2rem;
}
.location {
  font-family: "Lobster", cursive;
}
@-webkit-keyframes animo_1 {
  0% {
    -webkit-transform: translateX(0.7rem);
            transform: translateX(0.7rem);
  }
  50% {
    -webkit-transform: translateX(-0.5rem);
            transform: translateX(-0.5rem);
  }
  100% {
    -webkit-transform: translateX(0.7rem);
            transform: translateX(0.7rem);
  }
}
@keyframes animo_1 {
  0% {
    -webkit-transform: translateX(0.7rem);
            transform: translateX(0.7rem);
  }
  50% {
    -webkit-transform: translateX(-0.5rem);
            transform: translateX(-0.5rem);
  }
  100% {
    -webkit-transform: translateX(0.7rem);
            transform: translateX(0.7rem);
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

